import React, { useEffect, useRef, useState } from "react";
import Chart from "chart.js/auto";
import { getDatabase, ref, onChildAdded } from "firebase/database";
import { initializeApp } from "firebase/app";

const firebaseConfig = {
  apiKey: "AIzaSyB9uKYmvrtZZwYFo6WiWb6j_oO7pyM7-AE",
  authDomain: "backend1-79e56.firebaseapp.com",
  databaseURL: "https://backend1-79e56-default-rtdb.firebaseio.com",
  projectId: "backend1-79e56",
  storageBucket: "backend1-79e56.appspot.com",
  messagingSenderId: "327925233289",
  appId: "1:327925233289:web:fb3b6385f7f7cbcb9bb858",
  measurementId: "G-482MFGBLJV",
};

// Firebase  
const app = initializeApp(firebaseConfig);
const db = getDatabase(app);

function ChartComponent() {
  const chartRef = useRef(null);
  const canvasRef = useRef(null);
  const MAX_VISIBLE_POINTS = 20;
  const updateFrameRef = useRef(null);
  const [latestData, setLatestData] = useState(null);

  useEffect(() => {
    const ctx = canvasRef.current.getContext("2d");

    //     
    const savedChartData = JSON.parse(localStorage.getItem("chartData")) || {
      labels: [],
      data: [],
    };

    // Chart.js 
    const chartInstance = new Chart(ctx, {
      type: "line",
      data: {
        labels: savedChartData.labels,
        datasets: [
          {
            label: "  (%)",
            data: savedChartData.data,
            borderColor: "rgba(54, 162, 235, 1)",
            backgroundColor: "rgba(54, 162, 235, 0.2)",
            borderWidth: 2,
            pointRadius: 4,
            tension: 0.4,
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        interaction: {
          mode: "nearest",
        },
        scales: {
          x: {
            title: { display: true, text: "" },
          },
          y: {
            beginAtZero: true,
            max: 100,
            title: { display: true, text: " (%)" },
          },
        },
        plugins: {
          legend: { position: "top" },
        },
      },
    });

    chartRef.current = chartInstance;

    // Firebase   
    const humidityRef = ref(db, "humidityData");
    onChildAdded(humidityRef, (snapshot) => {
      const data = snapshot.val();
      if (data) {
        const { labels, datasets } = chartRef.current.data;

        const humidity = data.humidity;
        const timestamp = new Date(data.timestamp).toLocaleTimeString();

        //    
        setLatestData({
          humidity: humidity.toFixed(2),
          timestamp: new Date(data.timestamp).toLocaleString(),
        });

        //      
        if (!labels.includes(timestamp)) {
          labels.push(timestamp);
          datasets[0].data.push(humidity);

          //   
          if (labels.length > MAX_VISIBLE_POINTS) {
            labels.shift();
            datasets[0].data.shift();
          }

          // requestAnimationFrame   
          if (!updateFrameRef.current) {
            updateFrameRef.current = requestAnimationFrame(() => {
              chartRef.current.update();
              updateFrameRef.current = null;
            });
          }
        }
      }
    });

    //     
    const storageInterval = setInterval(() => {
      const { labels, datasets } = chartRef.current.data;
      localStorage.setItem(
        "chartData",
        JSON.stringify({
          labels,
          data: datasets[0].data,
        })
      );
    }, 5000);

    return () => {
      if (chartRef.current) {
        chartRef.current.destroy();
      }
      if (updateFrameRef.current) {
        cancelAnimationFrame(updateFrameRef.current);
      }
      clearInterval(storageInterval);
    };
  }, []);

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        minHeight: "100vh",
      }}
    >
      {/*   */}
      <main
        style={{
          flex: "1",
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
          padding: "20px",
        }}
      >
        <h1
          style={{
            marginBottom: "20px",
            fontSize: "2.2rem",
            color: "#2e7d32",
            textAlign: "center",
          }}
        >
            
        </h1>
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            gap: "20px",
            width: "100%",
            maxWidth: "900px",
            alignItems: "center",
          }}
        >
          {/*  */}
          <div
            style={{
              width: "100%",
              height: "400px",
              border: "1px solid #ddd",
              borderRadius: "10px",
              overflow: "hidden",
              backgroundColor: "#fff",
            }}
          >
            <canvas ref={canvasRef} style={{ width: "100%", height: "100%" }}></canvas>
          </div>

          {/*   */}
          <div
            style={{
              padding: "20px",
              border: "1px solid #ddd",
              borderRadius: "10px",
              backgroundColor: "#f9f9f9",
              width: "100%",
              maxWidth: "900px",
              textAlign: "center",
            }}
          >
            <h3 style={{ color: "#00529b", marginBottom: "10px" }}>
               
            </h3>
            {latestData ? (
              <>
                <p>
                  <strong> :</strong> {latestData.humidity}%
                </p>
                <p>
                  <strong> :</strong> {latestData.timestamp}
                </p>
              </>
            ) : (
              <p style={{ color: "#888" }}>  ...</p>
            )}
          </div>
        </div>
      </main>

      {/*  */}
      <footer
        style={{
          backgroundColor: "#2e7d32",
          color: "#fff",
          textAlign: "center",
          padding: "15px",
          fontSize: "1rem",
          marginTop: "auto",
        }}
      >
         2024   . All rights reserved.
      </footer>
    </div>
  );
}

export default ChartComponent;
