import React, { useEffect, useState, useCallback } from "react";
import { getDatabase, ref, onValue, query, orderByChild, startAt, endAt } from "firebase/database";
import { initializeApp } from "firebase/app";

const firebaseConfig = {
  apiKey: "AIzaSyB9uKYmvrtZZwYFo6WiWb6j_oO7pyM7-AE",
  authDomain: "backend1-79e56.firebaseapp.com",
  databaseURL: "https://backend1-79e56-default-rtdb.firebaseio.com",
  projectId: "backend1-79e56",
  storageBucket: "backend1-79e56.appspot.com",
  messagingSenderId: "327925233289",
  appId: "1:327925233289:web:fb3b6385f7f7cbcb9bb858",
  measurementId: "G-482MFGBLJV",
};

// Firebase  
const app = initializeApp(firebaseConfig);
const db = getDatabase(app);

function AnalysisPage() {
  const [tableData, setTableData] = useState([]); //  
  const [activeTab, setActiveTab] = useState('hour'); //    (hour, day, week)
  const [loading, setLoading] = useState(false); //  

  //      
  const getTimeRange = (tab) => {
    const now = new Date();
    switch (tab) {
      case 'hour': //  1
        return { start: now.getTime() - 60 * 60 * 1000, end: now.getTime() };
      case 'day': // 
        const startOfDay = new Date(now.setHours(0, 0, 0, 0));
        return { start: startOfDay.getTime(), end: Date.now() };
      case 'week': //  1
        return { start: now.getTime() - 7 * 24 * 60 * 60 * 1000, end: now.getTime() };
      default:
        return { start: 0, end: Date.now() };
    }
  };

  //   
  const loadData = useCallback((tab) => {
    setLoading(true);
    const { start, end } = getTimeRange(tab);

    const dataRef = ref(db, "humidityData");

    // Firebase   (    )
    const dataQuery = query(
      dataRef,
      orderByChild("timestamp"),
      startAt(new Date(start).toISOString()), //   
      endAt(new Date(end).toISOString()) //   
    );

    onValue(dataQuery, (snapshot) => {
      const data = snapshot.val();
      if (data) {
        const formattedData = Object.values(data).map((item) => ({
          timestamp: new Date(item.timestamp).toLocaleString(),
          temperature: item.temperature,  // 
          humidity: item.humidity,        // 
          soilMoisture: item.soilMoisture,  //  
        }));

        setTableData(formattedData.reverse()); //    
      } else {
        setTableData([]); //   
      }
      setLoading(false);
    });
  }, []);

  //     
  const handleTabClick = (tab) => {
    setActiveTab(tab);
    loadData(tab);
  };

  useEffect(() => {
    loadData(activeTab); //        
  }, [activeTab, loadData]);

  return (
    <div style={{ padding: "20px" }}>
      <h2 style={{ textAlign: "center" }}> </h2>

      {/*  UI */}
      <div style={{ display: "flex", justifyContent: "center", marginBottom: "20px" }}>
        <button
          onClick={() => handleTabClick('hour')}
          style={{
            padding: "10px 20px",
            backgroundColor: activeTab === 'hour' ? "#007bff" : "#f1f1f1",
            border: "none",
            cursor: "pointer",
            margin: "0 10px",
            color: activeTab === 'hour' ? "#fff" : "#007bff",
            borderRadius: "5px"
          }}
        >
           1
        </button>
        <button
          onClick={() => handleTabClick('day')}
          style={{
            padding: "10px 20px",
            backgroundColor: activeTab === 'day' ? "#007bff" : "#f1f1f1",
            border: "none",
            cursor: "pointer",
            margin: "0 10px",
            color: activeTab === 'day' ? "#fff" : "#007bff",
            borderRadius: "5px"
          }}
        >
          
        </button>
        <button
          onClick={() => handleTabClick('week')}
          style={{
            padding: "10px 20px",
            backgroundColor: activeTab === 'week' ? "#007bff" : "#f1f1f1",
            border: "none",
            cursor: "pointer",
            margin: "0 10px",
            color: activeTab === 'week' ? "#fff" : "#007bff",
            borderRadius: "5px"
          }}
        >
           1
        </button>
      </div>

      <div
        style={{
          marginTop: "20px",
          border: "1px solid #ddd",
          borderRadius: "10px",
          backgroundColor: "#f9f9f9",
          padding: "20px",
        }}
      >
        <h3 style={{ textAlign: "center" }}> </h3>
        <div
          style={{
            maxHeight: "400px",
            overflowY: "auto",
            border: "1px solid #ddd",
            padding: "10px",
          }}
        >
          <table style={{ width: "100%", borderCollapse: "collapse" }}>
            <thead>
              <tr style={{ backgroundColor: "#f4f4f4" }}>
                <th style={{ padding: "10px", border: "1px solid #ddd" }}></th>
                <th style={{ padding: "10px", border: "1px solid #ddd" }}> (C)</th>
                <th style={{ padding: "10px", border: "1px solid #ddd" }}> (%)</th>
                <th style={{ padding: "10px", border: "1px solid #ddd" }}>  (%)</th>
              </tr>
            </thead>
            <tbody>
              {tableData.length > 0 ? (
                tableData.map((row, index) => (
                  <tr key={index}>
                    <td style={{ padding: "10px", border: "1px solid #ddd" }}>
                      {row.timestamp}
                    </td>
                    <td style={{ padding: "10px", border: "1px solid #ddd" }}>
                      {row.temperature}
                    </td>
                    <td style={{ padding: "10px", border: "1px solid #ddd" }}>
                      {row.humidity}%  {/*     */}
                    </td>
                    <td style={{ padding: "10px", border: "1px solid #ddd" }}>
                      {row.soilMoisture}%  {/*       */}
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td
                    colSpan="4"
                    style={{
                      textAlign: "center",
                      padding: "10px",
                      border: "1px solid #ddd",
                    }}
                  >
                      ...
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
        {loading && <p style={{ textAlign: "center" }}>  ...</p>}
      </div>
    </div>
  );
}

export default AnalysisPage;
